<script>
export default {
    created:{

    },
    data() {
        return {
            selectNum:0
        }
    },
    mounted() {
      window.addEventListener("scroll", this.handleScroll, true); //监听滚动事件
    },
    methods: {
        handleScroll() {
            var scroll = Math.floor(document.body.scrollTop || document.documentElement.scrollTop || window.pageXOffset)
           try {
             if (scroll < 350) {
                
                this.$refs.headermenu.classList = ['header-menu'];
            } else{
                
                this.$refs.headermenu.classList = ['header-menu','sticky'];
            }
           } catch (error) {
               console.log(error);
             this.destroyed();   
           }
        }
    },
    destroyed() {
　　　　window.removeEventListener('scroll', this.handleScroll); // 销毁监听
　　}
}
</script>
